body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";

/* untitled-font-1 */
@font-face {
  font-family: "untitled-font-1";
  src:url("https://file.myfontastic.com/V6AYTwZg2axorXAeuLTFUn/fonts/1624476959.eot");
  src:url("https://file.myfontastic.com/V6AYTwZg2axorXAeuLTFUn/fonts/1624476959.eot?#iefix") format("embedded-opentype"),
    url("https://file.myfontastic.com/V6AYTwZg2axorXAeuLTFUn/fonts/1624476959.woff") format("woff"),
    url("https://file.myfontastic.com/V6AYTwZg2axorXAeuLTFUn/fonts/1624476959.ttf") format("truetype"),
    url("https://file.myfontastic.com/V6AYTwZg2axorXAeuLTFUn/fonts/1624476959.svg#1624476959") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "untitled-font-1" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "untitled-font-1" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-menu:before {
  content: "\64";
}
.icon-gh:before {
  content: "\65";
}
.icon-in:before {
  content: "\66";
}
.icon-fb:before {
  content: "\67";
}
.icon-mail:before {
  content: "\68";
}
.icon-tw:before {
  content: "\63";
}